import React from 'react';
import { Row, Col, Table, Button, Typography } from 'antd';
const { Title, Text } = Typography;

export const TrainingsCmp = ({ name, dataSource, columns, messages, goBack }) => {
  return (
    <div>
      <Row>
        <Col justify="end" type="flex" xs={24}>
          <Title style={{ float: 'left' }} level={4}>
            Model Name:
          </Title>
          <Text style={{ float: 'left', margin: '5px 0px 0px 20px' }} type="primary">
            {name}
          </Text>
          <Button
            style={{ margin: '0 0 10px 0', float: 'right' }}
            icon="rollback"
            type="primary"
            onClick={() => {
              goBack(false);
            }}
          >
            {messages.default.goBack}
          </Button>
        </Col>
        <Col xs={24}>
          <Table size="small" dataSource={dataSource} columns={columns} />
        </Col>
      </Row>
    </div>
  );
};
